.product-title {
  color: var(--third--color);
  font-family: var(--secondary-font);
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  flex-direction: column;

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 34px;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
  }

  @media screen and (min-width: 767px) and (max-width: 1279px) {
    font-size: 26px;
    line-height: 30px;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
  }
}

.accent-class {
  font-family: var(--secondary-font);
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 39px;
  letter-spacing: 0.04em;
  text-align: center;
  text-transform: uppercase;
  color: var(--secondary--color);

  @media screen and (min-width: 1280px) {
    letter-spacing: 0.06em;
    font-size: 58px;
    line-height: 66px;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
  }

  @media screen and (min-width: 767px) and (max-width: 1279px) {
    font-size: 48px;
    line-height: 55px;
    letter-spacing: 0.04em;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
  }
}

.product-card {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  width: calc(100% - 20px);
  flex-direction: column;
  padding-top: 140px;

  @media screen and (min-width: 1280px) {
    width: calc(100% - 100px);
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    flex-direction: row;
    padding-top: 260px;
  }

  @media screen and (min-width: 767px) and (max-width: 1279px) {
    width: calc(100% - 34px);
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    flex-direction: row;
    padding-top: 150px;
  }
}

.ice-cream {
  background: #FFB8CA;
  border-radius: 24px;

  &::after {
    content: '';
    position: absolute;
    width: 250px;
    height: 252px;
    top: 26%;
    left: 50%;
    transform: translate(-50%, -100%);
    background: url(".././images/products/ice-cream.png") no-repeat;
    background-size: cover;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;

    @media screen and (min-width: 1280px) {
      top: 25%;
      width: 335px;
      height: 335px;
      transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    }

    @media screen and (min-width: 767px) and (max-width: 1279px) {
      width: 220px;
      height: 224px;
      top: 23%;
      transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    }
  }
}

@media (min-device-pixel-ratio: 2),
(min-resolution: 192dpi),
(min-resolution: 2dppx) {
  .ice-cream::after {
    background: url(".././images/products/ice-cream@2x.png") no-repeat;
    background-size: cover;
  }
}

