.card {
  display: flex;
  flex-direction: column;
  width: calc(100% - 20px);
  margin-bottom: 130px;
  height: 485px;
  position: relative;
  transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;

  @media screen and (min-width: 1280px) {
    width: calc(33% - 30px);
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    height: 525px;
    margin-bottom: 120px;
  }

  @media screen and (min-width: 767px) and (max-width: 1279px) {
    width: calc(33% - 20px);
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    height: 485px;
    margin-bottom: 112px;
  }

  &__title {
    padding-top: 163px;
    font-family: var(--secondary-font);
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    @media screen and (min-width: 1280px) {
      font-size: 30px;
      line-height: 34px;
      transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    }
  }

  &__paragraph {
    font-family: var(--main-font);
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 152%;
    text-align: center;
    letter-spacing: 0.04em;
    padding: 70px 30px 0 30px;

    @media screen and (min-width: 1280px) {
      font-size: 16px;
      line-height: 176%;
      transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    }

    @media screen and (min-width: 767px) and (max-width: 1279px) {
      font-size: 14px;
      line-height: 176%;
      transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    }
  }

  &__img {
    margin: 0 auto;
    margin-top: 50px;
    cursor: pointer;
    border-radius: 50%;

    &:hover {
      border: 3px solid rgba(255, 255, 255, 0.5);
      transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    }
  }
}

.card::before {
  content: '...';
  position: absolute;
  font-size: 4rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
  text-align: center;
}